*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: white;
    font-family: "Roboto", sans-serif;
  }
  html{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
  .clear-button{
    background-color: transparent;
    border: 1px solid transparent;
  }
  /* navigation */
  
  
  
  
  
  /* common classes */
  .hide{
    display: none;
  }
  

  .border-bottom{
    border-bottom: 1px solid rgba(211, 211, 211, 0.1);
    padding-bottom: 10px;
  }
  .orange-button{
    background-color: #EB5928;
    color: white;
    font-size: 1.1rem;
    font-weight: bold;
    border-radius: 5px;
    border: none;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .orange-background{
    background-color: #EB5928;
  }
  
  .page{
    background-color: #111214;
    height: calc(100vh - 60px);
    overflow-y: auto;
    width: 100vw;
    padding: 20px 15px;
    position: relative;
    overflow-x: hidden;
  }
  @media (min-width: 800px) {
    .page{
      width: calc(100vw - 150px)
    }
  }
  .danger{
    background-color: #FF0000;
  }
  
  .input{
    height: 40px;
    background-color: white;
    border-radius: 5px;
    border: 1px solid transparent;
  }
  .full-width{
    width: 100%;
  }
  .half-width{
    width: calc(50% - 10px);
  }
  .large-button{
    width: 150px;
    height: 50px;
  }
  .medium-button{
    width: 100px;
    height: 50px;
  }
  .small-square{
    height: 30px;
