  }
  .medium-square{
    height: 50px;
    width: 50px;
  }
  .big-square{
    width: 75px;
    height: 75px;
  }
  .huge-square{
    width: 100px;
    height: 100px;
  }
  .medium-icon{
    width: 50px;
    height: 50px;
  }
  .small-icon{
    width: 25px;
    height: 25px;
  }
  
  .accent-color{
    color: #EB5928;
  }
