  /* Activity page */
  
  
  
  
  
  
  
  
  
  
  
  
  /* message modal */
  
  .message-modal{
    height: 100px;
    width: 90%;
    position: absolute;
    bottom: 80px;
    border-radius: 5px;
    border: 1px solid rgba(109, 109, 109, 0.418);
  }
  .message-modal h3{
    height: 40px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.363);
    padding-left: 10px;
    display: flex;
    align-items: center;
  }
  .message-modal .message-modal-text{
    padding: 10px;
  }
  .message-modal.success{
    background-color: #28a745;
  }
  .message-modal.fail{
    background-color: #F44336;
  }
  .message-modal.warning{
    background-color: #FFC107;
  }
  
  
  
  
  
  
  /* color picker */
  
  .color-picker{
    position: absolute;
    overflow: hidden;
    left: 5px;
    width: 40px;
    height: 40px;
    background-color: #1A1A1B;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    transition: all 0.1s;
  }
  .color-picker .color-button{
    width: 30px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid transparent;
  }
  .color-picker-header{
    height: 40px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  .color-picker .colors-container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  .extended-color-picker{
    width: 90% !important;
    height: 200px !important;
    transition: all 0.1s;
  }
  
  /* custom item creator */
  
  .custom-item-creator{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;
  }
  .custom-item-input{
    width: 100%;
  }
  
  .custom-item-creator button{
    width: 50px;
    height: 50px;
  }
  .custom-item-creator input{
    height: 40px;
    width: 100%;
    padding: 0 10px;
    border: 1px solid transparent;
    border-radius: 3px;
    color: black;
    font-size: 1.1rem;
  }
  .custom-item-creator img{
    width: 40px;
    height: 40px;
  }



.screen-toggle-buttons{
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #1A1A1B;
  border-radius: 5px;
}
.screen-toggle-buttons button{
  height: 100%;
  width: 50%;
  background-color: transparent;
  border: 1px solid transparent;
  font-size: 1.1rem;
  border-radius: 5px;
}
.screen-toggle-buttons > button:nth-child(1){
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;    
}
.screen-toggle-buttons > button:nth-child(2){
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;   
}
.screen-toggle-buttons button.selected-button{
  background-color: white;
  color: black;
  border-radius: 5px;
}
.screens-container{
  width: 100%;
  flex: 1;
  overflow-x: hidden;
}
.screens-container .screen{
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  transition: all 0.1s;
}
.screens-container .screen.left{
  float: left;
  animation: slideScreen 0.1s;
}
.screens-container .screen.right{
  float: right;
  animation: slideScreen 0.1s;
}

@keyframes slideScreen {
  from{
      width: 0;
      
  }
  to{
      width: 100%;
  }
}



.one-line-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* used to rotate arrow that is default pointed to the right */
.left{
  transform: rotateZ(180deg);
  transition: all 0.1s;
}
.down{
  transform: rotateZ(90deg);
  transition: all 0.1s;
}
.same-line-space{
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.input-error{
  color: red !important;
  border: 1px solid red !important;
}
.loading{
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.loading-icon{
  width: 60px;
  height: 60px;
  margin: 0 !important;
  animation: loadingSpin 1s infinite;
}
@keyframes loadingSpin {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(350deg);
  }
}





