.logs-history{
    width: 100%;
    overflow-y: auto;
    transition: all 0.1s;
    flex-direction: column;
    display: flex;
    max-height: 100%;
    border-radius: 5px;
}
.logs-history .logsContainer{
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    gap: 10px;  
}
